@import url(https://ciarancrocker.net/fonts/BebasNeue.css);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

html {
  overflow: hidden;
}

body {
  font-family: 'Open Sans', sans-serif;
  background-color: #333333;
}

.root {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  flex-grow: 1;
}

.osirisBar {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  background-color: #52d1dc;
  padding: 4px;
}

.osirisBar > h1 {
  font-family: 'Bebas Neue', sans-serif;
  margin: 4px;
  color: #333333;
}

@media all and (max-width: 800px) {
  .osirisBar {
    justify-content: space-around;
  }
}

@media all and (max-width: 500px) {
  .osirisBar {
    flex-direction: column;
  }
}

.osirisAuthentication {
  display: flex;
  flex: 1 1;
  align-items: center;
  justify-content: flex-end;
}

.osirisAuthentication > * {
  margin-left: 8px;
}

.osirisApp {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  flex-grow: 1;
}

.osirisEventSelect {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  background-color: #8d8e8e;
}

.osirisEvent {
  display: block;
  padding: 8px;
}

.osirisEvent:hover {
  background-color: #a7a7a7;
}

.osirisLayoutSelect {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  background-color: #a3a9aa;
}

.osirisLayout {
  display: block;
  padding: 8px;
}

.osirisLayout:hover {
  background-color: #bdc2c2;
}

.osirisLayoutView {
  min-width: 400px;
  max-width: 1200px;
  flex-grow: 1;
  background-color: #BBBBBF;
  margin: 0;
}

.osirisLayoutView > svg {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

circle {
  stroke: black;
  stroke-width: 1;
}

circle:hover {
  stroke: #333333;
  stroke-width: 2.5;
}

circle.picked {
  fill: red;
}

circle.unpicked {
  fill: green;
}

circle.unpickable {
  fill: grey;
}

.osirisSeatView {
  width: 256px;
  background-color: #f2f2f2;
  padding: 8px;
}

.osirisSeatView > h1 {
  margin: 0;
}

img.osirisProfileImg {
  border-radius: 50%;
  width: 96px;
  height: 96px;
  border: 1px solid #333333;
}

img.osirisLoggedInUserImg {
  border-radius: 50%;
  width: 32px;
  height: 32px;
  border: 1px solid #333333;
  margin-right: 4px;
}

